
#log[data-v-60601d42] {
	border-radius: 5px;
	box-shadow: 1px 1px 5px rgba(0,0,0,.5);
	overflow: hidden;
	padding: 12px 24px;
	box-sizing: border-box;
	margin-top: 20px;
	z-index: 1;
}
#log label[data-v-60601d42] {
	color : #fff;
}
#log .msg[data-v-60601d42] {
	color: green;
}
#log[data-v-60601d42]:before {
	content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: url('../assets/bj.jpg');
  z-index: -1;
  -webkit-filter: blur(20px);
  -moz-filter: blur(20px);
  -ms-filter: blur(20px);
  -o-filter: blur(20px);
  filter: blur(20px);
  background-position: center top;
	background-size: cover;
	background-attachment: fixed;
	margin: -30px;
}



